.list {
  margin: 0;
  padding: 0;
  max-height: 300px;
  overflow: scroll;

  list-style: none;
}
.text {
  margin: 0;
}
.contact {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  padding: 10px;

  border: 1px solid rgb(220 141 178);
  border-radius: 5px;
}
.contact:not(:last-child) {
  margin-bottom: 10px;
}
.name {
  margin-top: 0;
  font-weight: bold;
}
.phone {
  margin: 0;
  font-style: italic;
  color: rgba(0, 0, 0, 0.61);
}
.removeBtn {
  padding: 5px 15px;
  width: 160px;
  margin-left: 10px;

  background-color: rgb(126, 50, 150);
  color: aliceblue;

  border: none;
  border-radius: 5px;

  transition: background-color 250ms ease, transform 250ms ease;
}
.removeBtn:hover {
  background-color: rgb(162, 63, 192);
  transform: scale(1.1);
}
